module.exports = ".navbar {\n    background: white;\n    border-bottom: 1px solid black;\n    z-index: 10;\n}\n\n.navbar-brand {\n    font-size: 60px;\n    font-weight: lighter;\n}\n\n.nav-link {\n    font-size: 20px;\n    font-weight: lighter;\n    width: 150px;\n}\n\n.nav-item a {\n    border: 1px transparent;\n    -webkit-transition: border 0.33s;\n    transition: border 0.33s;\n}\n\n.nav-item a:hover {\n    cursor: pointer;\n}\n\n.pad-left { \n    padding-left: 50px;\n}\n\n.pad-right {\n    padding-right: 50px;\n}\n\n/* CSS for the app  */\n\n* {\n    font-weight: lighter;\n}\n\n.about {\n    height: 100px;\n    font-size: 40px;\n    font-weight: lighter;\n    text-align: right;\n    background-color: #4829fa;\n    color: white;\n    padding-right: 30px;\n    padding-top: 10px;\n}\n\n.carousel {\n    background-color: #4829fa;\n    color: white;\n    height: 600px;\n    font-size: 25px;\n    padding-top: 10px;\n    text-align: left;\n    padding-left: 30px;\n    padding-right: 20px;\n}\n\n.carousel-control-prev,\n.carousel-control-next{\n    bottom: -85%;\n}\n\n.carousel-indicators li {\n    width: 10px;\n    height: 10px;\n    border-radius: 100%;\n}\n\n.col-sm-6 {\n    display: inline-block;\n    padding: 0px;\n    margin-left: 0px;\n}\n\n.crop img{\n    width: 500px;\n    height: auto;\n    border-radius: 50%;\n    border: 1px solid black;\n    box-shadow: 0 0 4px 0 black;\n}\n\n.projects-title {\n    font-size: 60px;\n    text-align: right;\n    padding-right: 75px;\n}\n\n.projects {\n    height: 700px;\n    background-color: #4829fa;\n    color: white;\n}\n\n.resume {\n    background-color: #4829fa;\n    color: white;\n    position: relative;\n    max-width: 100%;\n    margin: 0 auto;\n}\n\n.resume::after {\n    content: '';\n    position: absolute;\n    width: 6px;\n    background-color: white;\n    top: 0;\n    bottom: 0;\n    left: 50%;\n    margin-left: -3px;\n}\n\n/* Container around content */\n\n.resume-container {\n    padding: 10px 30px;\n    position: relative;\n    background-color: inherit;\n    width: 50%;\n    color: black;\n}\n\n/* The circles on the timeline */\n\n.resume-container::after {\n    content: '';\n    position: absolute;\n    width: 35px;\n    height: 35px;\n    right: -17px;\n    background-color: #FF9F55;\n    border: 4px solid white;\n    top: 15px;\n    border-radius: 50%;\n    z-index: 1;\n}\n\n.resume-title {\n    font-size: 60px;\n    text-align: right;\n    padding-right: 75px;\n}\n\n/* Place the container to the left */\n\n.left {\nleft: 0px;\n}\n\n/* Place the container to the right */\n\n.right {\nleft: 50%;\n}\n\n/* Add arrows to the left container (pointing right) */\n\n.left::before {\ncontent: \" \";\nheight: 0;\nposition: absolute;\ntop: 23px;\nwidth: 0;\nz-index: 1;\nright: 20px;\nborder: medium solid white;\nborder-width: 10px 0 10px 10px;\nborder-color: transparent transparent transparent white;\n}\n\n/* Add arrows to the right container (pointing left) */\n\n.right::before {\ncontent: \" \";\nheight: 0;\nposition: absolute;\ntop: 23px;\nwidth: 0;\nz-index: 1;\nleft: 20px;\nborder: medium solid white;\nborder-width: 10px 10px 10px 0;\nborder-color: transparent white transparent transparent;\n}\n\n/* Fix the circle for containers on the right side */\n\n.right::after {\nleft: -16px;\n}\n\n/* The actual content */\n\n.content {\npadding: 20px 30px;\nbackground-color: white;\nposition: relative;\nborder-radius: 6px;\n}\n\n/* Media queries - Responsive timeline on screens less than 600px wide */\n\n@media screen and (max-width: 600px) {\n/* Place the timelime to the left */\n.timeline::after {\n    left: 31px;\n}\n\n/* Full-width containers */\n.container {\n    padding-left: 50px;\n    padding-right: 25px;\n}\n\n/* Make sure that all arrows are pointing leftwards */\n.container::before {\n    left: 60px;\n    border: medium solid white;\n    border-width: 10px 10px 10px 0;\n    border-color: transparent white transparent transparent;\n}\n\n/* Make sure all circles are at the same spot */\n.left::after, .right::after {\n    left: 15px;\n}\n\n/* Make all right containers behave like the left ones */\n.right {\n    left: 0%;\n}\n}\n/*# sourceMappingURL=data:application/json;base64, */"